import "./index.css";
import "./sentry";
import { KYC } from '../src/KYC';
var sessionBtn = document.getElementById("session-btn");
var sessionInput = document.getElementById("session-key");
var sessionEnvRadio = document.querySelectorAll('input[name="env"]');
var hash = (new URL(document.URL)).hash;
if (hash) {
    sessionInput.value = hash.substring(1);
}
var modal = document.getElementById("kyc-modal");
sessionBtn.onclick = function () {
    var selectedValue;
    for (var i in sessionEnvRadio) {
        if (sessionEnvRadio[i].checked) {
            selectedValue = sessionEnvRadio[i].value;
            break;
        }
    }
    var sessionKey = sessionInput.value;
    var container = document.getElementById("kyc-container");
    var kyc = new KYC(sessionKey, container, selectedValue);
    modal.style.display = "block";
    window.onclick = function (event) {
        if (event.target == modal) {
            modal.style.display = "none";
            kyc.cleanup();
        }
    };
    kyc.eventManager.addListener('error', function (e) {
        alert(e.errorCode + " => " + e.errorMessage);
    });
    kyc.eventManager.addListener('next_job', function (e) {
        var textBox = document.getElementById("text-box");
        switch (e.job) {
            case "face_agreement":
            case "face_text":
                textBox.innerText = e.job + " => plz say \"" + e.whatToSay + "\"";
                return;
            case "id_card":
                textBox.innerText = e.job + " => hold your id card in front of camera";
                return;
        }
    });
    kyc.eventManager.addListener('capture_progress', function (e) {
        var x = document.getElementById("capture-progress");
        x.value = e.progress;
    });
    kyc.eventManager.addListener('jobs', function (e) {
        var x = document.getElementById("jobs-list");
        while (x.lastElementChild)
            x.removeChild(x.lastElementChild);
        e.jobs.forEach(function (item) {
            var liEl = document.createElement('li');
            if (item.verified)
                liEl.classList.add("verified");
            liEl.innerText = item.id + ". " + item.action + ":" + item.verified;
            x.appendChild(liEl);
        });
    });
    kyc.eventManager.addListener('loading', function (e) {
        var container = document.getElementById("container");
        if (!e.isLoaded) {
            container.classList.add('loading');
            return;
        }
        container.classList.remove('loading');
    });
    kyc.eventManager.addListener('finish', function (e) {
        alert('finish');
        kyc.cleanup();
    });
};
